<template>
  <section>
    <el-form class="toolbar" :inline="true">
      <el-form-item label="검색">
          <el-row :gutter="5">
            <el-col :span="6">
              <el-select v-model="noticeOption" placeholder="Select">
                <el-option
                  v-for="item in options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-col>
            <el-col :span="14">
              <el-input v-model="search" @keyup.enter.native="onList" placeholder="검색어를 입력하세요"></el-input>
            </el-col>
            <el-col :span="4">
              <el-button type="primary" @click="onList">검색</el-button>
            </el-col>
        </el-row>
      </el-form-item>
    </el-form>

    <el-table :data="noticeData">
      <el-table-column prop="noticeMngNo" label="No" width="70" align="center"></el-table-column>
      <el-table-column prop="subj" label="제목" header-align="center">
        <template slot-scope="scope">
          <span class="link" @click="onDetail(scope.row.noticeMngNo)">
            {{scope.row.subj}}
          </span>
        </template>
      </el-table-column>
      <el-table-column prop="noticeTp" align="center" label="구분" width="150">
      </el-table-column>
      <el-table-column prop="regDt" align="center" label="등록일" width="220">
      </el-table-column>
    </el-table>

    <el-pagination layout="prev, pager, next" 
      :page-size="pageSize" 
      :total="total"
      :current-page.sync="currentPage" 
      @current-change="onPageChange"> 
    </el-pagination>

    <!-- @current-change -> 콜백함수로 페이지가 변경될 때 나타는 함수 -->
    
    <div class="bottomBtns">
      <el-button type="primary" @click="onSubmit">공지사항 등록</el-button>
    </div>
  </section>
</template>

<script>
  import axios from 'axios'
  import camelCase from 'camelcase-keys'
  import Cookies from 'js-cookie'
  import {noticeList} from '@/api/app'

  export default {
    data() {
      return {
        noticeData: [],
        total: 1,
        pageSize: 10,
        currentPage: 1,
        options: [{
          value: '10',
          label: '전체'
        }, {
          value: '20',
          label: '제목'
        }, {
          value: '30',
          label: '내용'
        }],
        noticeOption: '10',
        search: ''
      }
    },

    //created -> mounted 데이터를 많이 받아와야할 경우 
    mounted() {
      this.onList()
    },
    methods: {
      onList() {
        //.get('주소') -> 주소로 가서 데이터를 가져오라는 의미 
        //.post()
        // axios.get('http://localhost:3000/notice/list')
        noticeList({
          search: this.search
        })
          .then(res => {
            const data = camelCase(res.data.body)
            console.log('res = ', data) 
            // this.noticeData = data

            console.log('data.length = ', data.length)
            this.total = data.length

            let currentMaxLow = this.currentPage * this.pageSize
            let currentMinLow = currentMaxLow - this.pageSize

            console.log(currentMaxLow, currentMinLow)

            this.noticeData = data.slice(currentMinLow, currentMaxLow)
          })
          .catch(res => {

          })
          .finally(res => {

          })
      },

      onSubmit() {
       this.$router.push({name: 'NoticeRegister'})
      },

      onDetail(no) {
       console.log('no = ', no)
       this.$router.push({
         name: 'NoticeDetail',
         query: {no:no}
       })
      },

     onPageChange(pageNo) {
       console.log(pageNo)
       console.log(this.currentPage)
       this.onList()
     }
   },

   beforeRouteEnter (to, from, next) {
     if(Cookies.get('token')) { //로그인이 유무
      if(to.path === '/login') { //로그인 됨 && 로그인 페이지?
        next({path: '/'})  // 로그인 됨 && 로그인 페이지 맞음
      } else { // 로그인 됨 && 로그인 페이지 아님
        next() //그럼 그 페이지에 머무르기
      }
    } else { //로그인이 안됨
      if(to.path !== '/login') { //로그인 안됨 && 로그인 페이지가 아님?
        next({path: '/login'}) //로그인 안됨 && 로그인 페이지가 아님
      } else { //로그인 안됨 && 로그인 페이지가 맞음
        next() //로그인 페이지
      }
    }
   }
  }
</script>



